export default class Home extends Component<unknown> {
  currentRef: React.RefObject<HTMLDivElement>;
  alumniRef: React.RefObject<HTMLDivElement>;
  joinRef: React.RefObject<HTMLDivElement>;
  constructor(props: unknown) {
    super(props);
    this.currentRef = createRef();
    this.alumniRef = createRef();
    this.joinRef = createRef();
  }

  scrollToCurrent = () => {
    this.currentRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  scrollToAlumni = () => {
    this.alumniRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  scrollToJoin = () => {
    this.joinRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  renderUsers(users: Array<User>) {
    return (
      <Fragment>
        {users.map((user, index) => {
          return this.renderUser(user, index);
        })}
      </Fragment>
    );
  }

  renderUser(user: User, index: number) {
    let imgDivClass;
    let infoDivClass;
    if (index % 2 === 0) {
      imgDivClass = 'col-md-6 order-md-2';
      infoDivClass = 'col-md-6 flex-valign text-md-right';
    } else {
      imgDivClass = 'col-md-6 text-md-right';
      infoDivClass = 'col-md-6 flex-valign';
    }

    return (
      <div className="row mb-5">
        <div className={imgDivClass}>
          <img
            src={user.photo}
            className="img-fluid w-50 w-md-80 w-lg-60 rounded-circle labmemberimg shadow"
            alt="labmember"
          />
        </div>
        <div className={infoDivClass}>
          <h3 className="text-uppercase text-letter-spacing-xs mt-0 mb-1 text-dark font-weight-bold">
            {user.name}
          </h3>
          <h5 className="my-0 font-weight-normal">
            <em>
              {user.titles.map((title, index) => {
                if (index < user.titles.length - 1) {
                  return (
                    <Fragment>
                      {title}
                      <br />
                    </Fragment>
                  );
                } else {
                  return title;
                }
              })}
            </em>
          </h5>
          <hr className="hr-primary w-70 ml-0 ml-md-auto mr-md-0 mb-3" />
          {user.educationAndTraining ? (
            <p>
              <b>Education &amp; Training</b>
              <br />
              <ReactMarkdown className="plain-markdown">
                {user.educationAndTraining}
              </ReactMarkdown>
              <br />
            </p>
          ) : null}
          <p>
            <i className="fa fa-phone" aria-hidden="true"></i> {user.phone}
            <br />
            <i className="fa fa-envelope" aria-hidden="true"></i>{' '}
            <b>
              <a href={`mailto:${user.email}`}>{user.email}</a>
            </b>
            <br />
            {user.orcid ? (
              <Fragment>
                <img
                  className="inline-block"
                  style={{ height: '1em' }}
                  src={orcidid_logo}
                  alt="orcid logo"
                />{' '}
                <a
                  target="_blank"
                  href={`http://orcid.org/${user.orcid}`}
                  rel="noreferrer"
                >
                  0000-0003-3176-6829
                </a>
                <br />
              </Fragment>
            ) : null}
            {user.twitter ? (
              <Fragment>
                <img
                  className="inline-block"
                  style={{ height: '1em' }}
                  src={twitter2_logo}
                  alt="twitter logo"
                />{' '}
                <a
                  target="_blank"
                  href={`http://twitter.com/${user.twitter}`}
                  rel="noreferrer"
                >
                  @{user.twitter}
                </a>
                <br />
              </Fragment>
            ) : null}
            {user.linkedin ? (
              <Fragment>
                <img
                  className="inline-block"
                  style={{ height: '1em' }}
                  src={linkedin}
                  alt="linkedin logo"
                />{' '}
                <a
                  target="_blank"
                  href={`https://www.linkedin.com/in/${user.linkedin}`}
                  rel="noreferrer"
                >
                  ${user.linkedin}
                </a>
                <br />
              </Fragment>
            ) : null}
          </p>
        </div>
      </div>
    );
  }

  renderAlumni(alumni: Alumni) {
    return (
      <div className="col-md-4">
        <div className="service-box text-center module already-visible">
          <div style={{ padding: '0 0 1rem 0' }}>
            <img
              src={alumni.photo}
              alt=""
              className="img-fluid w-50 w-md-80 w-lg-60 rounded-circle labmemberimg shadow"
            />
          </div>
          <div className="service-content text-left">
            <h2 className="w-title">{alumni.name}</h2>
            <p>
              <b className="plain-markdown">
                <ReactMarkdown>{alumni.title}</ReactMarkdown>
              </b>
              Subsequently:
              <em className="plain-markdown first-p-inline">
                <ReactMarkdown>{alumni.subsequently}</ReactMarkdown>
              </em>
              <br />
              {alumni.orcid ? (
                <Fragment>
                  <img
                    className="inline-block"
                    style={{ height: '1em' }}
                    src={orcidid_logo}
                    alt="orcid logo"
                  />
                  <a
                    target="_blank"
                    href={`http://orcid.org/${alumni.orcid}`}
                    rel="noreferrer"
                  >
                    {alumni.orcid}
                  </a>
                  <br />
                </Fragment>
              ) : null}
              {alumni.twitter ? (
                <Fragment>
                  <img
                    className="inline-block"
                    style={{ height: '1em' }}
                    src={twitter2_logo}
                    alt="twitter logo"
                  />{' '}
                  <a
                    target="_blank"
                    href={`http://twitter.com/${alumni.twitter}`}
                    rel="noreferrer"
                  >
                    @{alumni.twitter}
                  </a>
                  <br />
                </Fragment>
              ) : null}
              {alumni.linkedin ? (
                <Fragment>
                  <img
                    className="inline-block"
                    style={{ height: '1em' }}
                    src={linkedin}
                    alt="linkedin logo"
                  />{' '}
                  <a
                    target="_blank"
                    href={`https://www.linkedin.com/in/${alumni.linkedin}`}
                    rel="noreferrer"
                  >
                    {alumni.linkedin}
                  </a>
                  <br />
                </Fragment>
              ) : null}
            </p>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <Fragment>
        <div
          className="intro intro-single route bg-image"
          style={{ backgroundImage: `url(${labOuting})` }}
        >
          <div className="overlay-mf"></div>
          <div className="intro-content display-table">
            <div className="table-cell">
              <div className="container">
                <h2 className="intro-title mb-4">Ruben Lab Members</h2>
                <div className="justify-content-center intro-subtitle">
                  <button
                    className="js-scroll btn btn-outline-light btn-sm"
                    style={{ fontSize: '1.5rem' }}
                    onClick={this.scrollToCurrent}
                  >
                    Current
                  </button>
                  <button
                    className="js-scroll btn btn-outline-light btn-sm"
                    style={{
                      fontSize: '1.5rem',
                      marginRight: '0.5rem',
                      marginLeft: '0.5rem',
                    }}
                    onClick={this.scrollToAlumni}
                  >
                    Alumni
                  </button>
                  <button
                    className="js-scroll btn btn-outline-light btn-sm"
                    style={{ fontSize: '1.5rem' }}
                    onClick={this.scrollToJoin}
                  >
                    JOIN
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <section className="portfolio-details">
          <div className="container py-7" ref={this.currentRef}>
            {this.renderUsers(users)}
          </div>
        </section>
        <section className="portfolio-mf sect-pt4 route">
          <div className="container" ref={this.alumniRef}>
            <hr />
            <div className="row">
              <div className="col-sm-12">
                <div className="title-box text-center">
                  <h3 className="title-a">Ruben Lab Alumni</h3>
                  <p className="subtitle-a">
                    They came, they conquered, they moved on to better things
                  </p>
                  <div className="line-mf"></div>
                </div>
              </div>
            </div>
            <div className="row">
              {alumnis.map((alumni) => this.renderAlumni(alumni))}
            </div>
          </div>
        </section>
        <section ref={this.joinRef} className="about-mf sect-pt0 route">
          <div className="container">
            <div className="row">
              <div className="col-sm-12">
                <div className="box-shadow-full">
                  <div className="row">
                    <div className="col-12">
                      <div className="service-ico">
                        <span className="ico-circle">
                          <i className="icon-person-add"></i>
                        </span>
                      </div>
                      <div className="title-box text-center">
                        <h3 className="title-a mt-3">Join our group</h3>
                        <div className="line-mf"></div>
                      </div>
                      <div className="title-box-2">
                        <h5 className="title-left">Graduate Students</h5>
                      </div>
                      <p className="lead">
                        Scripps labs do not directly accept students. All
                        prospective students must apply to the{' '}
                        <a href="https://education.scripps.edu/graduate/about-the-graduate-school/">
                          <b>graduate program</b>
                        </a>
                        . During the first year of the PhD program, Scripps
                        students can rotate in 2-4 labs prior to deciding on a
                        thesis lab. Our lab is usually open for rotation
                        students every quarter and will generally have room for
                        1-2 new graduate students per year.
                      </p>
                      <div className="title-box-2 mt-5">
                        <h5 className="title-left">Postdoctoral Fellows</h5>
                      </div>
                      <p className="lead">
                        Inquiries about postdoctoral positions should be emailed
                        directly to{' '}
                        <a href="mailto:ruben.fernandezbusnadiego@med.uni-goettingen.de">
                          <b>Ruben FERNÁNDEZ-BUSNADIEGO</b>
                        </a>
                        . Our lab welcomes applicants of any race, religion,
                        national origin, gender identity, caregiver and family
                        commitments, political affiliation, sexual orientation,
                        and eligible age or ability. Furthermore, we are
                        committed to maintaining a supportive and collaborative
                        lab environment. Our lab doesn’t post for specific
                        positions, so we encourage exploratory inquiries!
                      </p>

                      <p className="lead">
                        All candidates will be encouraged to explore applying
                        for extramural fellowships to support their research.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </Fragment>
    );
  }
}
