import styles from './app.module.scss';

import { Component } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
} from 'react-router-dom';
import { Nav, Navbar, Container } from 'react-bootstrap';

import ScrollRestoration from './components/ScrollRestoration';
import Home from './home';
import Publications from './publications';
import Group from './group';
import Contact from './contact';
import logoImage from '../assets/scripps-logo.png';

type AppState = {
  scrolled: number;
};

export class App extends Component<unknown, AppState> {
  constructor(props: unknown) {
    super(props);
    this.state = { scrolled: 0 };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.listenToScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.listenToScroll);
  }

  listenToScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;

    this.setState({
      scrolled: winScroll,
    });
  };

  backToTop() {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  }

  render() {
    const { scrolled } = this.state;
    const windowWidth = window.innerWidth;
    const whiteNav = (scrolled && scrolled > 82) || windowWidth < 992;
    const atTop = !scrolled;
    return (
      <Router>
        <ScrollRestoration />
        <Navbar
          expand="md"
          fixed="top"
          className={
            'navbar-b ' + (whiteNav ? 'navbar-reduce' : 'navbar-trans')
          }
        >
          <Container>
            <Navbar.Brand href="/">
              <img alt="lablogo" src="data:," className="lablogo" />
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav">
              <span></span>
              <span></span>
              <span></span>
            </Navbar.Toggle>
            <Navbar.Collapse
              id="basic-navbar-nav"
              className="justify-content-end"
            >
              <Nav>
                <li className="nav-item">
                  <NavLink
                    to="/"
                    exact
                    className="nav-link"
                    activeClassName="active"
                  >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    to="/publications"
                    className="nav-link"
                    activeClassName="active"
                  >
                    Publications
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    to="/group"
                    className="nav-link"
                    activeClassName="active"
                  >
                    Group
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    to="/contact"
                    className="nav-link"
                    activeClassName="active"
                  >
                    Contact
                  </NavLink>
                </li>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        <Switch>
          <Route path="/publications">
            <Publications />
          </Route>
          <Route path="/group">
            <Group />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
        <footer>
          <div className="container">
            <div className="row-sm-12">
              <p>
                <a href="https://www.scripps.edu">
                  <img
                    style={{ width: '22rem' }}
                    src={logoImage}
                    alt="gottingen logo"
                  />
                </a>
              </p>
              <div className="copyright-box">
                <div className="credits">
                  <p className="copyright">
                    © Copyright <strong>Ruben Lab</strong>. All Rights Reserved
                  </p>
                </div>
              </div>
            </div>
          </div>
        </footer>
        <button
          className="back-to-top"
          style={{ display: atTop ? 'none' : 'inline' }}
          onClick={this.backToTop}
        >
          <i className="fa fa-chevron-up"></i>
        </button>
      </Router>
    );
  }
}

export default App;
