type Link = {
  url: string;
  value: string;
};

type Journal = {
  name: string;
  number: string;
};

type PublicationBase = {
  name: string;
  author: string;
  associatedDescriptions?: Array<Array<Link> | Link>;
};

type PrePublication = PublicationBase & {
  preprintTime: string;
  doi: string;
};

type Publication = PublicationBase & {
  publishTime: string;
  journal: Journal;
  preprintLink?: string;
  preprintTime?: string;
  year: number;
  pmid?: number;
  pmcid?: number;
  downloadUrl?: string;
  openAccessUrl?: string;
};

type PublicationGroup = {
  year: number;
  publications: Array<Publication>;
};

