export default class Home extends Component {
  renderPublication(publication: PrePublication | Publication) {
    const prePublication = publication as PrePublication;
    const published = publication as Publication;
    return (
      <div
        className="container pub-box module already-visible"
        key={publication.name}
      >
        <div className="overlay-pub"></div>
        <div className="row">
          <div className="col-sm">
            <b>{publication.name}</b>
            <br />
            {publication.author}
            <br />
            {published.journal ? (
              <Fragment>
                <b>
                  <i>{published.journal.name}</i>
                </b>
                {' ; '}
                {published.journal.number}
                <br />
              </Fragment>
            ) : null}
            {(publication as Publication).publishTime ? (
              <Fragment>
                Published {(publication as Publication).publishTime}
              </Fragment>
            ) : (
              <Fragment>
                Online {(publication as PrePublication).preprintTime}
              </Fragment>
            )}
            <br />
            {(publication as PrePublication).doi ? (
              <Fragment>
                <b>DOI:</b>
                <a
                  href={`http://dx.doi.org/${
                    (publication as PrePublication).doi
                  }`}
                  target="_blank"
                  rel="noreferrer"
                >
                  {(publication as PrePublication).doi}
                </a>
              </Fragment>
            ) : null}
            {published.preprintLink ? (
              <Fragment>
                <img
                  alt="bioRxiv"
                  className="inline-block"
                  style={{ height: '1em' }}
                  src={biorxiv}
                ></img>{' '}
                <a
                  href={published.preprintLink}
                  target="_blank"
                  rel="noreferrer"
                >
                  Associated preprint
                </a>
                {' (posted '}
                {published.preprintTime})
                <br />
              </Fragment>
            ) : null}
          </div>
          <div className="col-sm-auto text-right">
            {published.pmid ? (
              <Fragment>
                <a
                  href={`https://www.ncbi.nlm.nih.gov/pubmed/${published.pmid}`}
                  target="_blank"
                  className="btn btn-outline-dark btn-sm pub-button"
                  rel="noreferrer"
                >
                  PMID {published.pmid}
                </a>
                <br />
              </Fragment>
            ) : null}
            {published.pmcid ? (
              <Fragment>
                <a
                  href={`http://ncbi.nlm.nih.gov/pmc/articles/${published.pmcid}`}
                  target="_blank"
                  className="btn btn-outline-dark btn-sm pub-button"
                  rel="noreferrer"
                >
                  PMCID {published.pmcid}
                </a>
                <br />
              </Fragment>
            ) : null}
            {published.downloadUrl ? (
              <Fragment>
                <a
                  target="_blank"
                  href={published.downloadUrl}
                  className="btn btn-outline-dark btn-sm pub-button mb-2"
                  rel="noreferrer"
                >
                  <i className="fa fa-download" aria-hidden="true"></i> Full
                  Text
                </a>
                <br />
              </Fragment>
            ) : null}
            {published.openAccessUrl ? (
              <Fragment>
                <a
                  target="_blank"
                  href={published.openAccessUrl}
                  className="btn btn-outline-dark btn-sm pub-button open-access mb-2"
                  rel="noreferrer"
                >
                  <img
                    alt="open access"
                    className="inline-block"
                    style={{ height: '1em' }}
                    src={openAccess}
                  />{' '}
                  Open Access
                </a>
                <br />
              </Fragment>
            ) : null}
            {publication.associatedDescriptions &&
            publication.associatedDescriptions.length > 0
              ? this.renderAssociatedDescriptions(
                  publication.associatedDescriptions
                )
              : null}
          </div>
        </div>
      </div>
    );
  }

  renderPublications(publications: Array<Publication>) {
    const groups: Array<PublicationGroup> = [];
    let currentYear = 0;
    let currentGroup: PublicationGroup | null = null;
    for (const pub of publications) {
      if (pub.year !== currentYear) {
        currentYear = pub.year;
        currentGroup = {
          year: pub.year,
          publications: [pub],
        };
        groups.push(currentGroup);
      } else {
        currentGroup?.publications.push(pub);
      }
    }
    return (
      <Fragment>
        {groups.map((g) => {
          return (
            <div className="row">
              <div className="col-sm-2 no-padding">
                <span className="year-label">{g.year}</span>
              </div>
              <div className="col-sm-10 no-padding-left">
                {g.publications.map((p) => this.renderPublication(p))}
              </div>
            </div>
          );
        })}
      </Fragment>
    );
  }

  renderAssociatedDescriptions(
    associatedDescriptions: Array<Array<Link> | Link>
  ) {
    return (
      <Fragment>
        <b>Associated Depositions:</b>
        <br />
        {associatedDescriptions.map((asso, index, arr) => {
          const isLast = index === arr.length - 1;
          if (Array.isArray(asso)) {
            return (
              <Fragment key={index}>
                {asso.map((link, i, links) => {
                  const lastLink = i === links.length - 1;
                  return (
                    <Fragment key={i}>
                      <a href={link.url} target="_blank" rel="noreferrer">
                        {link.value}
                      </a>
                      {lastLink ? null : ' | '}
                    </Fragment>
                  );
                })}
                {isLast ? null : <br />}
              </Fragment>
            );
          } else {
            return (
              <Fragment key={index}>
                <a href={asso.url} target="_blank" rel="noreferrer">
                  {asso.value}
                </a>
                {isLast ? null : <br />}
              </Fragment>
            );
          }
        })}
      </Fragment>
    );
  }

  render() {
    return (
      <Fragment>
        <div
          className="intro intro-single route bg-image"
          style={{ backgroundImage: `url(${literature})` }}
        >
          <div className="overlay-mf"></div>
          <div className="intro-content display-table">
            <div className="table-cell">
              <div className="container">
                <h2 className="intro-title mb-4">
                  Preprints &amp; Publications
                </h2>
              </div>
            </div>
          </div>
        </div>
        <section className="portfolio-details">
          <div className="container" id="preprints">
            <div className="row title-pub">
              <div className="col-sm-12">
                <div className="title-box-2">
                  <h5 className="title-left">Preprints (not peer-reviewed)</h5>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-sm-2">
                <img
                  alt="bioRxiv logo"
                  className="img-fluid img-logo"
                  src={bioRxivLogo}
                />
              </div>
              <div className="col-sm-10 no-padding-left">
                {prePublications.map((p) => this.renderPublication(p))}
              </div>
            </div>
          </div>
        </section>
        <section className="portfolio-mf route">
          <div className="container" id="publications">
            <div className="row title-pub">
              <div className="col-sm-12">
                <hr />
                <div className="title-box-2">
                  <h5 className="title-left">Peer-reviewed publications</h5>
                </div>
              </div>
            </div>
            {this.renderPublications(publications)}
          </div>
        </section>
      </Fragment>
    );
  }
}
